@font-face {
  font-family: icomoon;
  src:  url('../../static/fonts/icomoon.eot?7nrus9');
  src:  url('../../static/fonts/icomoon.eot?7nrus9#iefix') format('embedded-opentype'),
    url('../../static/fonts/icomoon.ttf?7nrus9') format('truetype'),
    url('../../static/fonts/icomoon.woff?7nrus9') format('woff'),
    url('../../static/fonts/icomoon.svg?7nrus9#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  font-family: icomoon, sans-serif !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-plus::before {
  content: "\e90a";
}

.icon-remove::before {
  content: "\e90b";
}

.icon-angle-right::before {
